import React, { Component } from 'react';
import PropTypes from 'prop-types';


import SliderOuter from './components/Sliderouter';

import OrderOuter from './componentsOrder/OrderOuter';




export default class Admin extends Component {
  constructor(props) {
    super(props);

    this.state = {
      exampleSetting: '',
      savedExampleSetting: '',
      slider:[],
      tab:0
    };

  }



  render() {

    const {tab} = this.state

    return (
      <div id="sliderDom" className="wrap">
        <div className="slideBox">
          <div className="tabs">
            <div className={(tab==0) ? 'tab active ': 'tab'} onClick={()=> this.setState({tab:0})} >考試設定</div>
            <div className={(tab==1) ? 'tab active ': 'tab'}onClick={()=> this.setState({tab:1})} >考試場地順序設定</div>
            <div className={(tab==2) ? 'tab active ': 'tab'} onClick={()=> this.setState({tab:2})} >授權報名中心順序設定</div>
            <div className={(tab==3) ? 'tab active ': 'tab'} onClick={()=> this.setState({tab:3})}>補習班順序設定</div>
          </div>
        

        {(tab==0)?
        <div className="tab_content">
          <SliderOuter wpObject={window.wpr_object}   />
        </div> : ''}
        {(tab==1)?
        <div className="tab_content">
          <OrderOuter wpObject={window.wpr_object}   post="location1" />
        </div> : ''}
        {(tab==2)?
        <div className="tab_content">
          <OrderOuter wpObject={window.wpr_object}  post="location2" />
        </div> : ''}
        {(tab==3)?
        <div className="tab_content">
          <OrderOuter wpObject={window.wpr_object}  post="location3" />
        </div> : ''}

        </div>
        
        

      </div>
    );
  }
}

Admin.propTypes = {
  wpObject: PropTypes.object
};
